import Layout from '@/views/layout'
import { Dashboard, Guide } from '@/views/admin'

const route = [
    {
        path: '/',
        component: Layout,
        redirect: '/dashboard',
        children: [
            {
                path: 'dashboard',
                component: Dashboard,
                name: 'Dashboard',
                meta: { title: 'dashboard', icon: 'dashboard', affix: true }
            }
        ],
    },
    {
        path: '/guide',
        component: Layout,
        redirect: '/guide/index',
        children: [
            {
                path: 'index',
                component: Guide,
                name: 'Guide',
                meta: { title: 'guide', icon: 'guide', noCache: true }
            }
        ]
    },
    {
        path: '/charts',
        component: Layout,
        redirect: 'noRedirect',
        meta: {
            title: 'charts',
            icon: 'chart'
        },
        children: [
            {
                path: 'index',
                component: () => import('@/views/admin/echarts'),
                name: 'Charts',
                meta: { title: 'charts', icon: 'chart', noCache: true }
            }
        ]
    },
]

export default route