import Layout from '@/views/layout'
// import RouterView from '@/components/routerView'

const route = {
    name: 'vue',
    path: '/vue',
    meta: {
        icon: 'lock'
    },
    component: Layout,
    // component: RouterView,
    redirect: '/vue/keepAlive/info',
    children: [
        {
            name: 'keepAlive',
            path: 'keepAlive',
            component: () => import('@/views/vue/keep-alive'),
            children: [
                {
                    name: 'info',
                    path: 'info',
                    component: () => import('@/views/vue/keep-alive/info')
                }, {
                    name: 'base',
                    path: 'base',
                    component: () => import('@/views/vue/keep-alive/base')
                }, {
                    name: 'routerView',
                    path: 'routerView',
                    component: () => import('@/views/vue/keep-alive/routerView'),
                    children: [
                        {
                            name: 'eg',
                            path: 'eg',
                            meta: {
                                keepAlive: true // 不需要缓存
                            },
                            component: () => import('@/views/vue/keep-alive/eg')
                        }, {
                            name: 'eg1',
                            path: 'eg1',
                            meta: {
                                keepAlive: false // 不需要缓存
                            },
                            component: () => import('@/views/vue/keep-alive/eg1')
                        },
                    ]
                }
            ]
        }, {
            name: 'component',
            path: 'component',
            component: () => import('@/views/vue/component')
        }
    ]
}

export default route