import Layout from '@/views/layout'

const route = [
    {
        path: '/tools',
        component: Layout,
        meta: { title: 'tools', icon: 'dashboard' },
        redirect: '/imgToTxt',
        children: [
            {
                path: 'imgToTxt',
                component: () => import('@/views/tools/ImgToTxt/index'),
                name: 'ImgToTxt',
                meta: { title: 'ImgToTxt', icon: 'dashboard' }
            },
            {
                path: 'Html2Canvas',
                component: () => import('@/views/tools/Html2Canvas/index'),
                name: 'Html2Canvas',
                meta: { title: 'Html2Canvas', icon: 'dashboard' }
            },
            {
                path: 'download',
                component: () => import('@/views/tools/download/index'),
                name: 'download',
                meta: { title: 'download', icon: 'dashboard' }
            }
        ],
    }
]

export default route